<template>
    <div class="o-29">
        <template v-for="(item, index) in number">
            <div class="span" :class="colorHandle(item * 1)" :key="index" >{{zodiac ? zodiacHandle(item * 1): item}}</div>
            <div class="span" v-if="index==number.length - 2" :key="index*2.64">
                <img :class="{'inverted': image}" src="@/static/img/97d2ce_200x200.png" alt="">
            </div>
        </template>
        <div class="span" @click.prevent="toggle">
            <img :class="{'inverted': image}" src="@/static/img/b77866_200x200.png" alt="">
        </div>
    </div>
</template>

<script>
export default {
    props: {
        number:{
            type: Array,
            default: [],
            required: true,
        },
        image:{
            type: Boolean,
            default: false,
        }
    },
    data(){
        return {
            zodiac: false,
        }
    },
    created(){
        
    },
    methods: {
        toggle(){
            this.zodiac = !this.zodiac
        },
        colorHandle(num){
            let hongboArr = [1, 2, 7, 8, 12, 13, 18, 19, 23, 24, 29, 30, 34, 35, 40, 45, 46];
            let lanboArr = [3, 4, 9, 10, 14, 15, 20, 25, 26, 31, 36, 37, 41, 42, 47, 48];
            let lvboArr = [5, 6, 11, 16, 17, 21, 22, 27, 28, 32, 33, 38, 39, 43, 44, 49];
            let red = hongboArr.contains(num)
            let blue = lanboArr.contains(num)
            let green = lvboArr.contains(num)
            if(red){
                return 'bg-red'
            }else if (blue){
                return 'bg-blue'
            }else{
                return 'bg-green'
            }       
        },
        zodiacHandle(num){
            var sxArr = new Array();
            var arr = ['鼠', '牛', '虎', '免', '龙', '蛇', '马', '羊', '猴', '鸡', '狗', '猪'];
            for (var i = 0; i < arr.length; i++){
                let obj = {
                    txt: arr[i],
                    number: '',
                }
                sxArr.push(obj)
            }
            sxArr[0].number = [1, 13, 25, 37, 49];
            sxArr[1].number = [12, 24, 36, 48];
            sxArr[2].number = [11, 23, 35, 47];
            sxArr[3].number = [10, 22, 34, 46];
            sxArr[4].number = [9, 21, 33, 45];
            sxArr[5].number = [8, 20, 32, 44];
            sxArr[6].number = [7, 19, 31, 43];
            sxArr[7].number = [6, 18, 30, 42];
            sxArr[8].number = [5, 17, 29, 41];
            sxArr[9].number = [4, 16, 28, 40];
            sxArr[10].number = [3, 15, 27, 39];
            sxArr[11].number = [2, 14, 26, 38];

            for (var i = 0; i < arr.length; i++){
                if(sxArr[i].number.contains(num)){
                    return sxArr[i].txt
                }
            }
            
        }
    }
}
</script>

<style lang="stylus" scoped>
    .o-29
        float left
        display flex
        .span
            float left
            width .4rem
            height .4rem
            border-radius 50%
            color #fff
            margin-left .1rem
            font-size .24rem
            display flex
            justify-content center
            align-items center
            &:first-child
                margin-left 0
            img
                width 80%    
    .bg-blue
        background #1b86fa
    .bg-green
        background #1fbd21
    .bg-red
        background #ee0223
    .inverted
        filter invert(100%)

</style>